.hero-main-box {
    background-color: white;
    width: 100%;
    height: 88vh;
    overflow: hidden;
    position: relative;
    border: 2px solid white;
    /* box-shadow: 1px 1px 2px 1px black; */
}

.hero-main-2 {
    position: relative;
    height: 100%;
    display: flex;
    flex-flow: row;
}

.images-hero-cont-test {
    /* width: 100%;
    height: 100%; */
    /* display: flex; */
    /* flex-shrink: 0; */
  height: 100%;
  position: relative;
  height: 100%;
  overflow: hidden; /* Ensure the gradient stays within bounds */

  overflow: hidden; /* Ensure the gradient stays within bounds */
  }
  
  .images-slider-main {
    width: 100%;
    height: 100%;
    object-fit: fill;
    object-position: center;
    filter: blur(2px); /* Blur effect */
  transform: scale(1.1); /* Slight zoom for aesthetic */
  }
  
  .images-hero-cont-test::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to right, rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0) 70%);
    pointer-events: none; /* Ensure the overlay doesn't block interactions */
    z-index: 1;
  }
  
  
.hero-welcome-text {
    font-size: 3em;
    color: #4CAF50;
    font-weight: 600;
    border-left: 6px solid #4CAF50;
    padding-left: 0.5em;
}

.hero-aislin-head {
    font-size: 7rem;
    color: black;
    font-weight: 400;
    margin-top: -0.2em;
}

.hero-formu-head {
    font-size: 7.5rem;
    margin-top: -0.8em;
    color: black;
    font-weight: 500;
    text-shadow: 4px 3px 8px black;
}

.hero-text-para {
    color: black;
    font-size: 1.7rem;
    width: 50vw;
    font-weight: 700;
    margin-top: -2.5em;
}

.hero-section-know-more-btn {
    width: 9vw;
    height: 6vh;
    background-color: #FF9800;
    border-radius: 20px;
    border: 2px solid #FF9800;
    color: white;
    font-size: 1vw;
}

/* Overlay Text */
.overlay-text-hero {
    position: absolute;
    top: 40%;
    left: 30%;
    transform: translate(-50%, -50%);
    text-align: start;
    display: flex;
    justify-content: start;
    align-items: start;
    flex-direction: column;
    z-index: 10;
    text-shadow: 0 2px 10px rgba(0, 0, 0, 0.8);
}

.hero-welcome-text,
.hero-aislin-head,
.hero-formu-head,
.hero-text-para {
    opacity: 0;
    animation: fadeIn 3s ease-in-out forwards;
}

/* Staggered transition effect */
.hero-welcome-text {
    transition-delay: 2s;
}

.hero-aislin-head {
    transition-delay: 2.2s;
}

.hero-formu-head {
    transition-delay: 2.4s;
}

.hero-text-para {
    transition-delay: 2.6s;
}

/* Animation for fading text in */
@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

/* Resetting text opacity for fresh animations */
.fade-in {
    animation: fadeIn 1s ease-in-out forwards;
}

