/* Download Catalog Section */
.download-catalog {
    position: fixed;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    z-index: 1000;
    background-color: #4CAF50;
    
    padding: 20px 20px;
    border-radius: 28px 0 0 28px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.download-btn {
    color: white;
    font-size: 0.8rem;
    /* text-overflow: ellipsis; */
    word-break: break-all;
    
    display: flex;
    justify-content: center;
    align-items: center;
    /* gap: 2em; */
    font-weight: bold;
    background-color: #4CAF50;
    border: none;
    cursor: pointer;
    height: 34vh;
    width: 0.1vw;
    padding: 30px 10px;
    border-radius: 20px;
    transition: background-color 0.3s ease-in-out;
}

.download-btn:hover {
    background-color: #45a049;
}

/* Contact Button Styles */
.contact-button {
    position: fixed;
    bottom: 20px;
    right: 20px;
    z-index: 1000;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 10px;
}

.main-contact-btn {
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 50%;
    width: 60px;
    height: 60px;
    font-size: 1.2rem;
    font-weight: bold;
    cursor: pointer;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease-in-out;
}

.main-contact-btn:hover {
    transform: scale(1.1);
}

.contact-options {
    display: flex;
    flex-direction: column;
    background-color: #ffffff;
    padding: 10px;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    gap: 10px;
}

.contact-options a {
    text-decoration: none;
    color: #4CAF50;
    font-weight: bold;
    padding: 5px 10px;
    border-radius: 5px;
    transition: background-color 0.3s ease-in-out;
}

.contact-options a:hover {
    background-color: #f1f1f1;
}

/* Open state for Contact Button */
.contact-button.open .main-contact-btn {
    background-color: #FF9800;
}


